
html {
  width: 100%;
  font-size: 10px;
}
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #2c3e50;
}
body {
  width: 100%;
  overflow-x: hidden;
  background-color: #fbfbfb;
}
input {
  outline: none;
}
html::-webkit-scrollbar {
  display: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
}
/* 路由滑动动画 */
.left-enter-active {
  -webkit-transition: all .3s;
  transition: all .3s;
}
.left-leave-active {
  -webkit-transition: all .3s;
  transition: all .3s;
}
.left-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.left-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-enter-active {
  -webkit-transition: all .3s;
  transition: all .3s;
}
.right-leave-active {
  -webkit-transition: all .3s;
  transition: all .3s;
}
.right-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
/* 媒体查询 */
